import IconButton from "@mui/material/IconButton";
import AddIcon from "@mui/icons-material/Add";
import { useState } from "react";
import {
  BottomBar,
  Container,
  QuestionImg,
  TopBar,
} from "./QuestionItem.styles";

function QuestionItem({
  question,
  selectedQuestionList,
  setSelectedQuestionList,
  selectedQuestionId,
}) {
  const [isHovering, setIsHovering] = useState(0);

  const addCart = () => {
    if (selectedQuestionId.includes(question.id)) {
      return;
    }
    setSelectedQuestionList([...selectedQuestionList, question]);
  };
  const source = `${question.year} ${question.month} ${question.grade} ${question.num}`;
  const point = `${(question.answerRate * 100).toFixed(2)}% ${
    question.point
  }`;
  return (
    <Container
      onMouseOver={() => setIsHovering(1)}
      onMouseOut={() => setIsHovering(0)}
    >
      <TopBar>
        <div>{question.id}</div>
        {isHovering === 1 ? (
          !selectedQuestionId.includes(question.id) ? (
            <IconButton
              color="primary"
              size="small"
              style={{ marginLeft: "auto" }}
              onClick={addCart}
            >
              <AddIcon fontSize="inherit" />
            </IconButton>
          ) : (
            <IconButton size="small" disabled style={{ marginLeft: "auto" }}>
              <AddIcon fontSize="inherit" />
            </IconButton>
          )
        ) : (
          ""
        )}
      </TopBar>
      <QuestionImg src={question.descriptionPath} />
      <BottomBar>
        <div>{source}</div>
        <div style={{ marginLeft: "auto" }}>{point}</div>
      </BottomBar>
    </Container>
  );
}
export default QuestionItem;
